import * as leanCloud from 'leancloud-storage/dist/av-weapp.js';
export default {
	data() {
		return {
			city: '',
			name: '',
			phone: '',
			area: '',
			aress: '',
		};
	},
	methods: {
		onCity: function(event) {
			this.city = event.target.value;
		},
		onName: function(event) {
			this.name = event.target.value;
		},
		onPhone: function(event) {
			this.phone = event.target.value;
		},
		onArea(event) {
			let _this = this
			_this.area = event.target.value
			
		},
		onAress(event) {
			this.aress = event.target.value;
		},
		submit() {
			let _this = this;
			if (_this.city.length < 2) {
				uni.showToast({
					title: '请正确填写城市',
					duration: 2000,
					icon: 'none'
				});
				return;
			}
			if (_this.name.length < 2) {
				uni.showToast({
					title: '请正确填写名字',
					duration: 2000,
					icon: 'none'
				});
				return;
			}
			if (_this.area == ''||_this.area == 0) {
				uni.showToast({
					title: '请正确填写房屋面积',
					duration: 2000,
					icon: 'none'
				});
				return;
			}
			if (_this.aress.length < 2) {
				uni.showToast({
					title: '请填写房屋地址',
					duration: 2000,
					icon: 'none'
				});
				return;
			}
			if (!/^[0-9]{11}$/.test(_this.phone)) {
				uni.showToast({
					title: '请正确填写手机号码',
					duration: 2000,
					icon: 'none'
				});
				return;
			}
			uni.showLoading({
				title: '提交中'
			});
			uni.setStorageSync('qqqq',1)
			var Todo = leanCloud.Object.extend('MyHouse');
			var todo = new Todo();
			todo.set('user', leanCloud.User.current());
			todo.set('city', _this.city);
			todo.set('name', _this.name);
			todo.set('phone', Number(_this.phone));
			todo.set('area', parseFloat(_this.area));
			todo.set('address', _this.aress); 
			todo.save().then(
				function(todo) {
					uni.hideLoading();
					uni.showToast({
						title: '添加成功',
						duration: 1000
					});
						uni.navigateTo({
							url: '../myhouse/myhouse'
						});
				},
				function(error) {
					uni.hideLoading();
					console.log(error)
					/* uni.navigateTo({
						url: '../submit-fail/submit-fail'
					}); */
				}
			);
		
		}
	},
	updated() {
	},
	created() {
	}
};
